}
@media (max-width: 991px) {
  #admin {
    /* .ant-table-body {
      overflow-x: scroll;
    } */
  }
  #admin .header {
    width: 100% !important;
  }
  #admin .main-layout {
    margin-left: 0px;
  }
  #admin .main-layout.collapsed {
    margin-left: 0px;
  }
  #admin .main-layout > .content {
    padding: 16px;
    margin: -80px 8px 0px;
  }
  #admin .main-layout .scheduleCard {
    width: 100%;
  }
  #admin .backTop {
    right: 20px;
    bottom: 20px;
  }
  #admin .search .wrapResult {
    margin: 8px;
  }
}
@media (max-width: 575px) {
  input {
    font-size: 16px !important;
  }
  #admin .header-search {
    display: none;
  }
}
